.textContainer {
  display: flex;
  flex-direction: column;
  margin-left: 5px;
  color: black;
  justify-content: space-between;
  margin-top: 2px;
  overflow:auto; 
}

.activeContainer {
  display: flex;
  padding: 2px;
  border: 1px solid rgb(210, 223, 230);
  height: 204px;
  border-radius: 5px;
}

.activeItem {
  display: flex;
  align-items: center;
}

.activeContainer img {
  padding-left: 10px;
}

.textContainer h1 {
  margin-bottom: 0px;
}

